











import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';

@Component
export default class RecordListItem extends Vue {
  @Prop({default: 0}) readonly type!: number; // 0 标示普通；1 是三级；2 是二级；3 是一级
  typeClassName = {
    '0': 'regular',
    '1': 'third',
    '2': 'second',
    '3': 'first'
  };
}
